@media only screen and (max-width: 600px) {
  .mainBody {
  display: none;  
  }
  
  .mapContact{
    display:flex;
    flex-direction: column;
  }

  .mapContact iframe {
    width:80vw;
  }

  .mapContact .contactInfo{
    width:80vw;
  }
}

.mainBody {
  width:100vw;
}

.contact-from {
    font-family: Roboto;
    background: #fefefe;
    border: none;
    box-shadow: 2px 2px 9px 5px #f5f5f5;
    border-radius: 10px;
    overflow: hidden;
    padding: 30px;
    border-radius: 10px; 
    max-width: 700px;
    width: 800px;
    margin: 20px auto 20px auto;
    border: 1px solid #e8e8e8;
  }
  
  .contact-from .title {
    text-align: center;
    text-transform: uppercase;
  }
  
  .contact-from textarea,
  .contact-from input[type="email"],
  .contact-from input[type="text"] {
    border: 1px solid #ccc;
    padding: 10px;
    width: 100%;
    border-radius: 3px;
    margin-bottom: 15px;
    font-size: 14px;
    font-family: arial;
    box-sizing: border-box;
  }
  
  .contact-from  label {
    margin-bottom: 5px;
    display:block;
  }
  
  .contact-from  label .required {
    color: #ff0000;
  }
  
  .contact-from .submit {
    text-align: center;
    margin-top: 0;
  }
  
  .contact-from  input[type="submit"] {
    background: #117CA6;
    color: #fff;
    border: none;
    padding: 12px 20px;
    font-size: 14px;
    border-radius: 25px;
    min-width: 100px;
    box-shadow: 2px 2px 9px 5px #f5f5f5;
  }
  
  .contact-from input[type="submit"]:hover {
    cursor: pointer;
  }

.mapContact{
    display: flex;
    justify-content:space-around;
    align-items:center;
    margin-top: 50px;
    width:100vw;
    gap:20px;
    margin-bottom:50px;
}

iframe {
  width:50%;
  height:450px;
  border:0;
}

.contactInfo {
  width:50%; 
  height:150px; 
  background-color:#83c5be;
  border-radius:10px;
  padding:15px; 
  overflow:scroll;
  scroll-behavior: smooth;
}

