.container-slider {
  max-width: 1000px;
  height: 500px;
  margin:50px auto ;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 700px){
  .container-slider {
    margin: 20px auto;
  }
}
  
.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}

.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
  
.moveSlide {
  opacity: 1;
}
  
.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background: #b0d767;
  border: 1px solid rgba(34, 34, 34, 0.287);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
  

.prev {
  top: 50%;
  left: 20px;
  transform: translateY(-60%);
}
  
.next {
  top: 50%;
  right: 20px;
  transform: translateY(-60%);
}